import StyledFirebaseAuth from "react-firebaseui/StyledFirebaseAuth";
import { Paper } from "@material-ui/core";
import firebase from "firebase";
import Logo from "./../../assets/images/fb_text.png";
import signature from "./../../assets/images/signature.png";
import Style from "./Style";
const Login = () => {
  const classes = Style();
  const uiConfig = {
    signInFlow: "popup",
    signInOptions: [firebase.auth.GoogleAuthProvider.PROVIDER_ID],
  };

  return (
    <div className={classes.login__container}>
      <Paper elevation={3} className={classes.login}>
      <div className={classes.logo}>
          <img src={Logo} alt="Logo" />
          <h4>Clone</h4>
        </div>
        <form className={classes.form} onSubmit={(e) => e.preventDefault()}>
          <input  type="email" value="" placeholder="Email Address or phone number" />
          <input  type="password" value="" placeholder="Password" />
          <button >Log In</button>
          <p>Forgotten Password ?</p>
        </form>
        <div className={classes.google}>
          <section>
            <div></div>
            <p>OR</p>
            <div></div>
          </section>
          <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />
        </div>
        <img src={signature} alt="linked-in-logo" width="200px"  height="80px"/>
      </Paper>
    </div>
  );
};
export default Login;
